.mms--toolbar-input {
  background-color: rgba(255, 255, 255, .2);
  border: none;
  border-radius: 3px;
  color: white;
  height: 26px;
  margin: auto;
  padding: 2px 8px;
  margin: auto;
  margin-left: 2px;
  margin-right: 2px;
}

.mms--toolbar-input:focus {
	background-color: rgba(255, 255, 255, .25);
}
.mms--editor {
	min-height: 120px;
	background-color: white;
	padding: 20px 22px;
}
.mms--toolbar {
	background-color: black;
	padding: 4px;
	color: white;
	border-radius: 4px;
	overflow: hidden;
	display: -webkit-box;
	display: flex;
}

/* prevent toolbar from receiving focus while selecting text */
body:active .mms--toolbar {
  pointer-events: none;
}
.mms--toolbar-button {
	border-radius: 4px;
	padding: 0 4px;
	height: 32px;
	min-width: 32px;
	display: -webkit-box;
	display: flex;
	-webkit-box-align: center;
	        align-items: center;
	-webkit-box-pack: center;
	        justify-content: center;
	border: none;
	background-color: black;
	border: 1px solid black;
	color: white;
	font-size: 14px;
	font-weight: 500;
	-webkit-user-select: none;
	   -moz-user-select: none;
	    -ms-user-select: none;
	        user-select: none;
}

.mms--toolbar-button svg {
	fill: white;
	width: 14px;
	height: 14px;
}

.mms--toolbar-button:focus {
	border: 1px solid rgba(255, 255, 255, .3);
}

.mms--toolbar-button.mms--active {
	background-color: rgba(255, 255, 255, .3);
}

.mms--toolbar-button:hover {
	cursor: pointer;
	background-color: rgba(255, 255, 255, .2);
}

.mms--toolbar-button.mms--disabled {
	color: rgba(255, 255, 255, .6);
}
.mms--toolbar-button.mms--disabled svg {
	fill: rgba(255, 255, 255, .6);
}

.mms--toolbar-button.mms--disabled:hover {
	cursor: default;
	background-color: inherit;
}

.mms--toolbar-spacer {
	width: 1px;
	height: 22px;
	background-color: rgba(255, 255, 255, .2);
	margin: auto;
	margin-left: 6px;
	margin-right: 6px;
}

.mms--toolbar-back {
	font-size: 11px !important;
}
.mms--editor {
	color: #435a6f;
	font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Open Sans,Helvetica Neue,Icons16,sans-serif;
	font-size: 16px;
  line-height: 1.5;
  letter-spacing: 0;
  font-weight: 400;
  text-transform: none;
  text-rendering: optimizeSpeed;
  -webkit-text-size-adjust: 100%;
     -moz-text-size-adjust: 100%;
      -ms-text-size-adjust: 100%;
          text-size-adjust: 100%;
  -webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

/* leafs
 * .mms--editor b {}
 * .mms--editor i {}
 * .mms--editor u {}
 * .mms--editor s {}
 */

/* elements
 * .mms--editor p {}
 * .mms--editor h1 {}
 * .mms--editor h2 {}
 * .mms--editor h3 {}
 * .mms--editor ul {}
 * .mms--editor ol {}
 * .mms--editor li {}
 * .mms--editor blockquote {}
 */

 .mms--editor blockquote {
   margin: 0;
   padding: 10px 18px;
   background-color: whitesmoke;
   border-radius: 4px;
 }

.mms--editor *:first-child { margin-top: 0; }
.mms--editor *:last-child { margin-bottom: 0; }